import {call, put, takeLatest, fork, all} from "redux-saga/effects";

import {
  fetchError,
  fetchStart,
  fetchSuccess,
} from "../actions";
import {
  SUGGEST,
} from "../constants/actionTypes/Suggest";
import suggestAPI from "../../services/suggest";
import {suggestSuccess} from "../actions/Suggest";

// Pin search
function* suggestRequest({payload}) {
  const {query, metadata} = payload;

  let params;
  if (metadata) {
    params = metadata;
  }

  try {
    yield put(fetchStart());

    let data;
    if (params) {
      const result = yield call(suggestAPI.suggest, query, params);
      data = result.data;
    } else {
      const result = yield call(suggestAPI.suggest, query);
      data = result.data;
    }

    yield put(suggestSuccess(data, metadata));
    yield put(fetchSuccess());
  } catch (error) {
    yield put(fetchError())
  }
}

function* suggestSaga() {
  yield takeLatest(SUGGEST, suggestRequest)
}

export default function* rootSaga() {
  yield all([
    fork(suggestSaga),
  ]);
}
