import {
  SUGGEST,
  SUGGEST_SUCCESS,
} from "../constants/actionTypes/Suggest";


export const suggest = (query, metadata) => {
  return {
    type: SUGGEST,
    payload: {
      query,
      metadata
    }
  }
};

export const suggestSuccess = (data, metadata) => {
  return {
    type: SUGGEST_SUCCESS,
    payload: {
      data,
      metadata
    }
  }
};

