import {
  SUGGEST_SUCCESS,
} from "../constants/actionTypes/Suggest";

const INIT_PIN_STATE = {
  collections: [],
  didYouMean: "",
  pages: [],
  productPerPage: 0,
  products: [],
  query: "",
  suggestions: [],
};

export default (state = INIT_PIN_STATE, action) => {
  switch (action.type) {
    case SUGGEST_SUCCESS: {
      const {data} = action.payload;

      return {
        ...state,
        ...data,
      }
    }

    default:
      return state;
  }
}
