import {
  FETCH_ERROR, FETCH_ITEM_2_ERROR, FETCH_ITEM_2_START, FETCH_ITEM_2_SUCCESS,
  FETCH_ITEM_ERROR,
  FETCH_ITEM_START,
  FETCH_ITEM_SUCCESS,
  FETCH_PARTIAL_ERROR,
  FETCH_PARTIAL_START,
  FETCH_PARTIAL_SUCCESS,
  FETCH_START,
  FETCH_SUCCESS,
  HIDE_MESSAGE,
  SHOW_ERROR_MESSAGE,
  SHOW_MESSAGE
} from '../constants/actionTypes/Common';

const INIT_STATE = {
  error: "",
  loading: false,
  partialLoading: false,
  itemLoading: false,
  item2Loading: false,
  message: '',
};

const DEFAULT_ERROR_MESSAGE = "An Error occurred, please refresh and try again.";

const getErrorMsg = (payload) => {
  return payload || DEFAULT_ERROR_MESSAGE;
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    // Fetch
    case FETCH_START: {
      return {...state, error: '', message: '', loading: true};
    }
    case FETCH_SUCCESS: {
      return {...state, error: '', message: '', loading: false};
    }
    case FETCH_ERROR: {
      return {...state, loading: false, error: getErrorMsg(action.payload), message: ''};
    }

    // Fetch partial
    case FETCH_PARTIAL_START: {
      return {...state, error: '', message: '', partialLoading: true};
    }
    case FETCH_PARTIAL_SUCCESS: {
      return {...state, error: '', message: '', partialLoading: false};
    }
    case FETCH_PARTIAL_ERROR: {
      return {...state, partialLoading: false, error: getErrorMsg(action.payload), message: ''};
    }

    // Fetch item
    case FETCH_ITEM_START: {
      return {...state, error: '', message: '', itemLoading: true};
    }
    case FETCH_ITEM_SUCCESS: {
      return {...state, error: '', message: '', itemLoading: false};
    }
    case FETCH_ITEM_ERROR: {
      return {...state, itemLoading: false, error: getErrorMsg(action.payload), message: ''};
    }

    // Fetch item 2
    case FETCH_ITEM_2_START: {
      return {...state, error: '', message: '', item2Loading: true};
    }
    case FETCH_ITEM_2_SUCCESS: {
      return {...state, error: '', message: '', item2Loading: false};
    }
    case FETCH_ITEM_2_ERROR: {
      return {...state, item2Loading: false, error: getErrorMsg(action.payload), message: ''};
    }

    // show messages
    case SHOW_MESSAGE: {
      return {...state, error: '', message: action.payload, loading: false};
    }
    case SHOW_ERROR_MESSAGE: {
      return {...state, loading: false, error: action.payload, message: ''};
    }

    // hide message
    case HIDE_MESSAGE: {
      return {...state, loading: false, error: '', message: ''};
    }
    default:
      return state;
  }
}
