import {useCallback} from "react";
import {suggest} from "../appRedux/actions";
import {useDispatch} from "react-redux";

const useHandleInput = (settings) => {
  const dispatch = useDispatch();

  return useCallback(e => {
    // Set query
    const q = e.target.value.trim();
    if (!q) {
      return
    }
    if (settings && settings.pinnedProducts) {
      if (q in settings.pinnedProducts) {
        dispatch(suggest(q, {pinnedProducts: settings.pinnedProducts[q]}));
        return
      }
    }
    dispatch(suggest(q));
  }, [dispatch, settings]);
};

export default useHandleInput;
