import React, { useState, useEffect, useCallback, useContext } from "react";
import {useSelector} from "react-redux";
import { InstantSearch } from "./components";
import { getInstantSearchPosition } from "./utils";
import { ThemeContext } from "styled-components";
import useHandleInput from "./hooks/useHandleInput";

const InstantSearchWrapper = props => {
  const { input, settings } = props;
  const [position, setPosition] = useState({top: 0, left: 0, maxHeight: 0});
  const [enable, setEnable] = useState(false);
  const themeContext = useContext(ThemeContext);
  const [widgetWidth, setWidgetWidth] = useState(0);

  const suggestData = useSelector(({suggest}) => suggest);
  const {query} = suggestData;

  // handler when user typing
  const handleInput = useHandleInput(settings);


  // // Handle when use touch to result (Only for mobile)
  // const handleOnTouchMove = useCallback(() => {
  //   // input.blur();
  //   document.activeElement.blur();
  // }, [input]);

  // Get instant search widget width from theme
  useEffect(() => {
    const regex = /(\d+)/gm;
    const width = parseInt(regex.exec(themeContext.sizes[1])[1]);
    setWidgetWidth(width);
  }, [themeContext.sizes]);

  // Calculate instant search widget position if window resize or user typing
  const calculateWidgetPosition = useCallback(() => {
    // Calculate Widget position based on Input position
    const { top, left, maxHeight } = getInstantSearchPosition(input, widgetWidth);
    setPosition({top, left, maxHeight});

  }, [input, widgetWidth]);

  useEffect(() => {
    window.addEventListener('resize', calculateWidgetPosition);
    input.addEventListener('input', calculateWidgetPosition);
    return () => {
      window.removeEventListener('resize', calculateWidgetPosition);
      input.removeEventListener('focus', calculateWidgetPosition);
    }
  }, [calculateWidgetPosition, input]);

  // To handle show or hide instant search when user focus or blur to input
  const handleShowWidget = useCallback(eventType => {
    return () => {
      // Hide results list & show other elemennts
      if (eventType === "focusout") {
        setEnable(true);
      } else if (eventType === "focus") {
        setEnable(true);
      }
    };
  }, []);

  useEffect(() => {
    // Add event listener to input
    input.addEventListener("input", handleInput);

    const eventTypes = ["focus", "focusout"];
    eventTypes.forEach(function(eventType) {
      input.addEventListener(eventType, handleShowWidget(eventType));
    });

    // Clear events after unmount
    return () => {
      document.removeEventListener("input", handleInput);
      eventTypes.forEach(eventType => {
        document.removeEventListener(eventType, handleShowWidget(eventType));
      });
    };
  }, [handleInput, handleShowWidget, input]);

  const showWidget =
    enable &&
    query;

  return <InstantSearch {...suggestData} enable={showWidget} { ...position } input={input}/>;
};

export default InstantSearchWrapper;
