import {
  FETCH_ERROR, FETCH_ITEM_2_ERROR, FETCH_ITEM_2_START, FETCH_ITEM_2_SUCCESS,
  FETCH_ITEM_ERROR,
  FETCH_ITEM_START,
  FETCH_ITEM_SUCCESS,
  FETCH_PARTIAL_ERROR,
  FETCH_PARTIAL_START,
  FETCH_PARTIAL_SUCCESS,
  FETCH_START,
  FETCH_SUCCESS,
  SHOW_ERROR_MESSAGE,
  SHOW_MESSAGE,
} from "../constants/actionTypes/Common";

export const fetchStart = () => {
  return {
    type: FETCH_START
  }
};

export const fetchSuccess = () => {
  return {
    type: FETCH_SUCCESS
  }
};

export const fetchError = (error) => {
  return {
    type: FETCH_ERROR,
    payload: error
  }
};

// Partial fetch
export const fetchPartialStart = () => {
  return {
    type: FETCH_PARTIAL_START
  }
};

export const fetchPartialSuccess = () => {
  return {
    type: FETCH_PARTIAL_SUCCESS
  }
};

export const fetchPartialError = (error) => {
  return {
    type: FETCH_PARTIAL_ERROR,
    payload: error
  }
};

// Partial item
export const fetchItemStart  = () => {
  return {
    type: FETCH_ITEM_START
  }
};

export const fetchItemSuccess = () => {
  return {
    type: FETCH_ITEM_SUCCESS
  }
};

export const fetchItemError = (error) => {
  return {
    type: FETCH_ITEM_ERROR,
    payload: error
  }
};

// Partial item 2
export const fetchItem2Start = () => {
  return {
    type: FETCH_ITEM_2_START,
  }
};

export const fetchItem2Success = () => {
  return {
    type: FETCH_ITEM_2_SUCCESS,
  }
};

export const fetchItem2Error = (error) => {
  return {
    type: FETCH_ITEM_2_ERROR,
    payload: error
  }
};

export const showMessage = (message) => {
  return {
    type: SHOW_MESSAGE,
    payload: message
  }
};

export const showErrorMessage = (message) => {
  return {
    type: SHOW_ERROR_MESSAGE,
    payload: message
  }
};

