import axios from "axios";

import config from "../config";
import {encodeQueryData} from "./utils";

const API_ENDPOINT = config.apiEnpoint;

const suggest = (query, params) => {
  // build param
  let url = `${API_ENDPOINT}/guest/shopify/suggest?q=${query}`;
  if (params) {
    url = `${API_ENDPOINT}/guest/shopify/suggest?q=${query}&${encodeQueryData(params)}`;
  }
  return axios({
    url,
    method: "get",
  })
};

const suggestAPI= {
  suggest
};

export default suggestAPI;

